<script>
	export let name;
	export let current;
</script>

<button class:active={current === name} on:click={() => (current = name)}>
	<slot />
</button>

<style>
	button {
		font-size: 80%;
		background: none;
		border: none;
		outline: none;
		margin-left: 2em;
	}
	button:hover {
		color: #ccc;
	}
	button.active {
		color: white;
	}
</style>
