<script>
	import Card from "./Card.svelte";
	import Intro from "./Intro.svelte";
	import { fade, fly } from "svelte/transition";
	//import doesn't need curlies if it is a default export?
	import { utils, fluids } from "../utils";
	// import { fluids } from "../utils";

	let sdigs = 4,
		wdigs = 6,
		sds = utils.sds,
		sdw = utils.sdw,
		D,
		Ds = sds(1.5),
		y,
		ys = sds(0.5),
		alpha,
		alphaC,
		theta,
		thetaRad,
		thetaC,
		thetaCRad,
		yQ,
		ss = sds(0.2),
		s,
		n = 0.013,
		g = 9.81,
		A,
		AQ,
		T,
		TQ,
		WP,
		WPQ,
		R,
		R2,
		RQ,
		Q,
		QQs = 10,
		QQ,
		v,
		vQ,
		E,
		EQ,
		NF,
		NFQ,
		yc,
		ycQ,
		Ac,
		AcQ,
		vc,
		vcQ,
		Emin,
		EminQ,
		WPc,
		WPcQ,
		Rc,
		RcQ,
		Sc,
		ScQ;

	let toDegrees = utils.toDegrees,
		toRadians = utils.toRadians;
	let specifyY = true;
	let validS = true;
	let typeOfChannel = "circular";

	calculate();

	// calculateFromQ();

	document.addEventListener("DOMContentLoaded", katexify);
	document.addEventListener("input", katexify);
	document.addEventListener("click", katexify);
	document.addEventListener("change", katexify);

	function calculate() {
		utils.sdigs = 7;
		s = Number(ss);
		s = s < 0 ? -s : s;
		// ss = sds(s); // for binding to the input field
		validS = s === 0 ? false : true;

		n = n <= 0 ? 0.01 : n;
		g = g < 9.8 ? 9.8 : g;

		D = Number(Ds);
		D = D < 0 ? 0 : D;

		y = Number(ys);
		y = y < 0 ? -y : y;
		y = y > D ? D : y;
		// for binding to the input field

		QQ = Number(QQs);
		QQ = QQ < 0 ? -QQ : QQ;
		QQs = sds(QQ); // for binding to the input field

		if (validS) {
			calculateFromY();
			calculateFromQ();
		}
	}

	function calculateFromY() {
		const r = D / 2;
		alpha = getAlpha(y, r);
		theta = getTheta(y, r);
		thetaRad = toRadians(theta);
		A = getArea(thetaRad, D);
		WP = getWP(thetaRad, D);
		R = getR(thetaRad, D);
		v = fluids.getV(n, R, s);
		Q = getQ();
		E = fluids.getE(y, v, g);
		T = getT(thetaRad, D);
		NF = fluids.getNF(v, A, T, g);
		yc = getYc(Q);
		thetaCRad = sdw(toRadians(getTheta(yc, r)));
		thetaC = sdw(toDegrees(thetaCRad));

		Ac = getArea(thetaCRad, D);
		vc = Number(sdw(Q / Ac));
		Emin = fluids.getE(yc, vc, g);
		// WPc = Number(sd(getWP(yc), wdigs));
		Rc = getR(thetaCRad, D);
		Sc = fluids.getCriticalSlope(n, vc, Rc);
	}

	function calculateFromQ() {
		// yQ = Number(sd(getY(), wdigs));
		// AQ = Number(sd(getArea(yQ), wdigs));
		// vQ = Number(sd(QQ / AQ, wdigs));
		// EQ = Number(sd(yQ + vQ ** 2 / g / 2, wdigs));
		// TQ = Number(sd(d + (zl + zr) * yQ, wdigs));
		// NFQ = Number(sd(fluids.getNF(vQ, AQ, TQ), wdigs));
		// ycQ = Number(sd(getYc(QQ), wdigs));
		// AcQ = Number(sd(getArea(ycQ), wdigs));
		// vcQ = Number(sd(QQ / AcQ, wdigs));
		// EminQ = Number(sd(+ycQ + vcQ ** 2 / 2 / g, wdigs));
		// WPcQ = Number(sd(getWP(ycQ), wdigs));
		// RcQ = Number(sd(getR(ycQ), wdigs));
		// ScQ = Number(sd(getCriticalSlope(vcQ, RcQ), wdigs));
	}

	// inputs specific to each channel section
	const inputFormat = utils.debounce(() => {
		ss = sds(s);
		ys = sds(y);
		Ds = sds(D);
	}, utils.delay);

	const processChange = () => {
		calculate();
		inputFormat();
	};

	function getAlphaRad(y, r) {
		return Number(sdw(Math.acos(Math.abs(r - y) / r)));
	}

	function getAlpha(y, r) {
		return Number(sdw(toDegrees(getAlphaRad(y, r))));
	}

	function getThetaRad(y, r) {
		if (y <= r) {
			return Number(sdw(2 * getAlphaRad(y, r)));
		}
		return Number(sdw(2 * (Math.PI - getAlphaRad(y, r))));
	}

	function getTheta(y, r) {
		return Number(sdw(toDegrees(getThetaRad(y, r))));
	}

	function getArea(thetaR, D) {
		return Number(sdw(((thetaR - Math.sin(thetaR)) * D ** 2) / 8));
	}

	function getWP(thetaR, D) {
		return Number(sdw((thetaR * D) / 2));
	}

	function getR(thetaR, D) {
		return Number(sdw((((thetaR - Math.sin(thetaR)) / thetaR) * D) / 4));
	}

	function getQ() {
		return Number(sdw(A * v));
	}

	function getT(alphaRad, D) {
		if (alphaRad === 0) return 0;
		return Number(sdw(D * Math.sin(alphaRad / 2)));
	}

	function getNFfromYDQ(y, D, Q) {
		const r = D / 2;
		const alphaRad = getAlphaRad(y, D / 2);
		// console.log("alphaRad: " + alphaRad);
		const thetaRad = getThetaRad(y, r);
		// console.log("thetaRad: " + thetaRad);
		const T = getT(thetaRad, D);
		// console.log("T: " + T);
		const A = getArea(thetaRad, D);
		const v = Q / A;
		// console.log("v: " + v);
		const NF = fluids.getNF(v, A, T, g);
		// console.log("NF0: " + NF);
		return Number(sdw(fluids.getNF(v, A, T, g)));
	}

	function getYc(Q, shallow = 0, deep = 0.9 * D) {
		const delta = 1 / 10 ** wdigs,
			mid = (deep + shallow) / 2;

		if (Math.abs(deep - shallow) < delta) {
			return Number(sdw((deep + shallow) / 2));
		}

		// do the search
		if (getNFfromYDQ(mid, D, Q) < 1) {
			return getYc(Q, shallow, mid);
		} else {
			return getYc(Q, mid, deep);
		}
	}
</script>

<main>
	<section class="intro">
		<Intro {typeOfChannel} />

		<label class="yQlayout">
			<input type="radio" bind:group={specifyY} value={true} />
			<span class="radio__label">Specify !$y!$</span>
		</label>

		<label class="yQlayout">
			<input type="radio" bind:group={specifyY} value={false} />
			<span>Specify !$Q!$</span>
		</label>

		<p>
			Input values and answers are accurate to 4 significant digits. Interim
			calculations are calculated to 6 significant digits to prevent the
			accumulation of rounding errors.
		</p>
	</section>

	<section class="fig">
		<div class="width90">
			{#if specifyY}
				<div class:hide={!(y < D / 2)} class:show={y < D / 2}>
					<img
						src="./images/circularChannelSectionUnderHalf.png"
						alt="circular channel section by depth under half full"
					/>
				</div>
				<div class:hide={!(y == D / 2)} class:show={y == D / 2}>
					<img
						src="./images/circularChannelSectionHalf.png"
						alt="circular channel section by depth half"
					/>
				</div>
				<div class:hide={!(y == D)} class:show={y == D}>
					<img
						src="./images/circularChannelSectionFull.png"
						alt="circular channel section by depth full"
					/>
				</div>
				<div class:hide={!(y > D / 2 && y < D)} class:show={y > D / 2 && y < D}>
					<img
						src="./images/circularChannelSectionOverHalf.png"
						alt="circular channel section by depth over half full"
					/>
				</div>
			{/if}

			<form on:submit|preventDefault={calculate}>
				<label class="D">
					!$D=!$
					<input
						type="number"
						step="any"
						required
						bind:value={Ds}
						on:input={processChange}
					/>
					!$\mathsf&lbrace;m&rbrace;!$
				</label>

				{#if specifyY}
					<label class="y">
						!$y=!$
						<input
							type="number"
							step="any"
							required
							bind:value={ys}
							on:input={processChange}
						/>
						!$\mathsf&lbrace;m&rbrace;!$
					</label>
				{:else}
					<label class="Q">
						!$Q=!$
						<input
							type="number"
							step="any"
							required
							bind:value={QQs}
							on:input={processChange}
						/>
						!$\mathsf&lbrace;m^3\!/s&rbrace;!$
					</label>
				{/if}
			</form>
		</div>

		<form on:submit|preventDefault={calculate}>
			<label class="s">
				Channel slope, !$S=!$
				<input
					type="number"
					step="any"
					required
					bind:value={ss}
					on:input={processChange}
				/>
				!$\small \% !$
			</label>
			<label class="n">
				Manning's !$n=!$
				<input
					type="number"
					step="0.000001"
					required
					bind:value={n}
					on:input={calculate}
				/>
				!$\small \mathsf &lbrace; s/m^&lbrace;1/3&rbrace; &rbrace; !$
			</label>
			<label class="g">
				!$g=!$
				<input
					type="number"
					step="0.000001"
					required
					bind:value={g}
					on:input={calculate}
				/>
				!$\small \mathsf &lbrace; m/s^2 &rbrace; !$
			</label>
		</form>
	</section>

	<!-- //////////////////////////////////////////////   FROM y   ///////////////////////// -->

	<section class="results">
		{#if specifyY}
			{#if !validS}
				<section class="message">
					For a slope of 0, there is no flow. And, without a flow, critical
					conditions are not defined.
				</section>
			{:else}
				<section class="normal">
					<h1>Normal (Uniform) Flow</h1>
					{#if y < D / 2}
						<Card
							answer=" !$ \alpha = {sds(alpha)}^\circ!$"
							solution="$$
						\begin&lcub;aligned&rcub;
							\alpha &= \cos^&lcub;-1&rcub;\left(\frac&lcub;OA&rcub;&lcub;OB&rcub;\right) \\
							 	&= \cos^&lcub;-1&rcub;\left(\frac&lcub;D/2-y&rcub;&lcub;D/2&rcub;\right) \\
							 	&= \cos^&lcub;-1&rcub;\left(\frac&lcub;{sds(D)}/2-{sds(y)}&rcub;&lcub;{sds(
								D
							)}/2&rcub;\right) \\
								&= {sdw(alpha)}^\circ \\
						\end&lbrace;aligned&rbrace; $$"
						/>

						<Card
							answer=" !$ \theta = {sds(
								theta
							)}^\circ \hskip&lbrace;1em&rbrace;(={sds(
								thetaRad
							)}\,\mathsf&lbrace;rad&rbrace;)!$"
							solution="$$
						\begin&lcub;aligned&rcub;
							\theta &= 2\alpha \\
							 	&= 2({sdw(alpha)}^\circ) \\
								&= {sdw(theta)}^\circ\\\\
							\theta_\mathsf&lbrace;rad&rbrace; &= \theta \cdot \frac \pi&lbrace;180^\circ&rbrace; \\
								&= {sdw(theta)}^\circ \cdot \frac \pi&lbrace;180^\circ&rbrace; \\
								&= {sdw(thetaRad)}
						\end&lbrace;aligned&rbrace; $$"
						/>
					{:else if y > D / 2 && y < D}
						<Card
							answer=" !$ \alpha = {sds(alpha)}^\circ!$"
							solution="$$
						\begin&lcub;aligned&rcub;
							\alpha &= \cos^&lcub;-1&rcub;\left(\frac&lcub;OA&rcub;&lcub;OB&rcub;\right) \\
							 	&= \cos^&lcub;-1&rcub;\left(\frac&lcub;y-D/2&rcub;&lcub;D/2&rcub;\right) \\
							 	&= \cos^&lcub;-1&rcub;\left(\frac&lcub;{sds(y)}-{sds(D)}/2&rcub;&lcub;{sds(
								D
							)}/2&rcub;\right) \\
								&= {sdw(alpha)}^\circ \\
						\end&lbrace;aligned&rbrace; $$"
						/>

						<Card
							answer=" !$ \theta = {sds(
								theta
							)}^\circ \hskip&lbrace;1em&rbrace;(={sds(
								thetaRad
							)}\,\mathsf&lbrace;rad&rbrace;)!$"
							solution="$$
						\begin&lcub;aligned&rcub;
							\theta &= 360^\circ-2\alpha \\
							 	&= 360^\circ-2({sdw(alpha)}^\circ) \\
								&= {sdw(theta)}^\circ\\\\
							\theta_\mathsf&lbrace;rad&rbrace; &= \theta \cdot \frac \pi&lbrace;180^\circ&rbrace; \\
								&= {sdw(theta)}^\circ \cdot \frac \pi&lbrace;180^\circ&rbrace; \\
								&= {sdw(thetaRad)}
						\end&lbrace;aligned&rbrace; $$"
						/>
					{/if}

					{#if y === D / 2}
						<Card
							answer="Area, !$A = {sds(A)} \,\mathsf&lbrace;m^2&rbrace; !$"
							solution="$$
							\begin&lcub;aligned&rcub;
								A &= \frac&lbrace;\pi D^2&rbrace;&lbrace;8&rbrace; \\							
								&= \frac&lbrace;\pi ({sds(D)})^2&rbrace;&lbrace;8&rbrace;	\\						
								&= {sdw(A)} \,\mathsf&lbrace;m^2&rbrace;							
							\end&lbrace;aligned&rbrace; 
						$$"
						/>

						<Card
							answer="Hydraulic Radius, !$R = {sds(
								R
							)} \,\mathsf&lbrace;m&rbrace; !$"
							solution="$$
							\begin&lcub;aligned&rcub;
								P &= \frac&lbrace;\pi D&rbrace;&lbrace;2&rbrace; \\				
								&= \frac&lbrace;\pi({sds(D)})&rbrace;&lbrace;2&rbrace; \\							
								&= {sdw(WP)} \,\mathsf&lbrace;m&rbrace;\\\\
							R &= A/P \\
							&= \frac&lbrace;{sdw(A)}&rbrace;&lbrace;{sdw((Math.PI * D) / 2)}&rbrace; \\
							&={sdw(R)}\,\mathsf&lbrace;m&rbrace;							
							\end&lbrace;aligned&rbrace; $$"
						/>
					{:else if y === D}
						<Card
							answer="Area, !$A = {sds(A)} \,\mathsf&lbrace;m^2&rbrace; !$"
							solution="$$
							\begin&lcub;aligned&rcub;
								A &= \frac&lbrace;\pi D^2&rbrace;&lbrace;4&rbrace;		\\			&= \frac&lbrace;\pi ({sds(
								D
							)})^2&rbrace;&lbrace;4&rbrace;	\\						
								&= {sdw(A)} \,\mathsf&lbrace;m^2&rbrace;				
							\end&lbrace;aligned&rbrace; $$"
						/>

						<Card
							answer="Hydraulic Radius, !$R = {sds(
								R
							)} \,\mathsf&lbrace;m&rbrace; !$"
							solution="$$
							\begin&lcub;aligned&rcub;
								P &= \pi D \\			
								&= \pi({sds(D)}) \\							
								&= {sdw(WP)} \,\mathsf&lbrace;m&rbrace;\\\\
							R &= A/P \\
							&= \frac&lbrace;{sdw(A)}&rbrace;&lbrace;{sdw(Math.PI * D)}&rbrace; \\
							&={sdw(R)}\,\mathsf&lbrace;m&rbrace;							
							\end&lbrace;aligned&rbrace; $$"
						/>
					{:else}
						<Card
							answer=" Area, !$A = {sds(A)} \,\mathsf&lbrace;m^2&rbrace; !$"
							solution="$$
								\begin&lcub;aligned&rcub;
									A &= \frac&lbrace;\left(\theta-\sin&lbrace;\theta&rbrace;\right)D^2&rbrace;&lbrace;8&rbrace;							
								\end&lbrace;aligned&rbrace; 
								$$
								<div class='width85'>
								This area formula is for !$\theta!$ in radians. We could have calculated the value of !$\theta!$ above in radians by changing from degree mode to radian mode on our calculator but then there is the real possibility of forgetting to change back to degree mode later. An alternative method is to simply convert !$\theta!$ from degrees to radians, multiplying degrees by !$\pi/180^\circ!$, <br/>i.e. !${sdw(
								theta
							)}^\circ\times\pi/180^\circ={sdw(thetaRad)}!$ rad. <p>
								We can leave !$\theta!$ in degrees when applying the sine function<br/> since !$\sin&lbrace;{sdw(
								theta
							)}^\circ&rbrace; =\sin&lbrace;{sdw(thetaRad)}&rbrace;!$ rad. Then,
								</div>
								$$
								\begin&lcub;aligned&rcub;							
									A	&= \frac&lbrace;\left({sdw(thetaRad)}-\sin&lbrace;{sdw(
								theta
							)}^\circ&rbrace;\right)\left({sds(
								D
							)}\right)^2&rbrace;&lbrace;8&rbrace; \\
										&= {sdw(A)}\,\mathsf&lbrace;m^2&rbrace;							
								\end&lbrace;aligned&rbrace; $$"
						/>

						<Card
							answer="Hydraulic Radius, !$R = {sds(
								R
							)} \,\mathsf&lbrace;m&rbrace; !$"
							solution="$$
								\begin&lcub;aligned&rcub;
									R 	&= \left[\frac&lbrace;\theta_\mathsf&lbrace;rad&rbrace;-\sin\theta&rbrace;&lbrace;\theta_\mathsf&lbrace;rad&rbrace;&rbrace;\right]\cdot\frac D4 \\	
										&= \left[\frac&lbrace;{sdw(thetaRad)}-\sin{sdw(
								theta
							)}^\circ&rbrace;&lbrace;{sdw(
								thetaRad
							)}&rbrace;\right]\cdot\frac &lbrace;{sds(D)}&rbrace;4 \\	
										&= {sdw(R)}\,\mathsf&lbrace;m&rbrace;							
								\end&lbrace;aligned&rbrace; 
							$$
							
							$$\textcolor&lbrace;gray&rbrace;&lbrace; \left[ \textsf &lbrace; Or:\qquad  &rbrace; \normalsize
								\begin&lcub;matrix&rbrace;
									P &\!\!\!=&\!\!\! \frac&lbrace;\normalsize\theta_\mathsf&lbrace;rad&rbrace; \cdot D&rbrace;&lbrace;\normalsize 2&rbrace; 
										&\!\!\!=&\!\!\! \frac&lbrace;\normalsize{sdw(thetaRad)}(\normalsize{sds(
								D
							)})&rbrace;&lbrace;\normalsize 2&rbrace; 
										&\!\!\!=&\!\!\! {sdw(
								(sdw(thetaRad) * sds(D)) / 2
							)}\,\mathsf&lbrace;m&rbrace;\\[1em]
									R 	&\!\!\!=&\!\!\! \frac&lbrace;\normalsize A&rbrace;&lbrace;\normalsize P&rbrace; 	
										&\!\!\!=&\!\!\! \frac&lbrace;\normalsize{sdw(A)}&rbrace;&lbrace;\normalsize{sdw(
								WP
							)}&rbrace; 	
										&\!\!\!=&\!\!\! {sdw(A / WP)}\,\mathsf&lbrace;m&rbrace;						
								\end&lbrace;matrix&rbrace; \right] &rbrace;$$"
						/>
					{/if}

					<Card
						answer=" Average flow velocity, !$v = {sds(
							v
						)} \,\mathsf&lbrace;m/s&rbrace; !$"
						solution="$$
						\begin&lcub;aligned&rcub;
							v 	&= \frac&lbrace;1&rbrace;&lbrace;n&rbrace;R^&lbrace;2/3&rbrace;S^&lbrace;1/2&rbrace; \\	
							 	&= \frac&lbrace;1&rbrace;&lbrace;{n}&rbrace;({sdw(
							R
						)})^&lbrace;2/3&rbrace;({sds(s / 100)})^&lbrace;1/2&rbrace; \\						 	
								&= {sdw(v)}\,\mathsf&lbrace;m/s&rbrace;							
						\end&lbrace;aligned&rbrace;  $$"
					/>

					<Card
						answer=" Volume flow rate, !$Q = {sds(
							Q
						)} \,\mathsf&lbrace;m^3\!/s&rbrace; \hskip&lbrace;1em&rbrace;(={sds(
							Q * 1000
						)}\,\mathsf&lbrace;L/s&rbrace;)!$"
						solution="$$
						\begin&lcub;aligned&rcub;
							Q 	&= Av \\	
							 	&= ({sdw(A)})({sdw(v)})\\
								&= {sdw(Q)}\,\mathsf&lbrace;m^3/s&rbrace;							
						\end&lbrace;aligned&rbrace; $$"
					/>
					<Card
						answer="Specific Energy, !$E = {sds(
							E
						)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							E 	&= y+\frac&lbrace;v^2&rbrace;&lbrace;2g&rbrace; \\	
							 	&= {sds(y)}+\frac&lbrace;{sdw(v)}^2&rbrace;&lbrace;2({g})&rbrace; \\						 	
								&= {sdw(E)}\,\mathsf&lbrace;m&rbrace;							
						\end&lbrace;aligned&rbrace; $$"
					/>

					<Card
						answer="Free Surface, !$T = {sds(T)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							T 	&= D\sin (\theta_\mathsf&lbrace;rad&rbrace;/2) \\	
							 	&= {sds(D)}\sin ({sdw(thetaRad)}_\mathsf&lbrace;rad&rbrace;/2) \\
						&= {sds(D)}\sin ({sdw(theta)}^\circ/2) \\					 	
								&= {sdw(T)}\,\mathsf&lbrace;m&rbrace;							
						\end&lbrace;aligned&rbrace; $$"
					/>
					<Card
						answer="Froude Number, !$N_F = {sds(NF)}!$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							N_F &=  \frac&lbrace;v&rbrace;&lbrace;\sqrt&lbrace;g(A/T)&rbrace;&rbrace; \\
								&=  \frac&lbrace;{v}&rbrace;&lbrace;\sqrt&lbrace;{g}({sdw(A)}/{sdw(
							T
						)})&rbrace;&rbrace; \\
								&= {sdw(NF)}
						\end&lbrace;aligned&rbrace;
					$$"
					/>
				</section>
			{/if}

			<section class="critical">
				<h1>Critical Flow</h1>
				{#if thetaCRad > Math.PI}
					<Card
						answer="For !$Q={sdw(
							Q
						)}\,\mathsf&lbrace;m^3\!/s&rbrace;!$, Critical Depth, !$y_c = {sds(
							yc,
							sdigs
						)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="Trial-and-error may be used, adjusting depth of flow !$y_c!$ until the depth yields an associated Froude Number, !$N_F!$, equal to !$1!$. This is somewhat time-consuming and error-prone. Alternatively:
						
						$$
						\begin&lbrace;aligned&rbrace;
							N_F &= 1 \\
							\Rightarrow v_c	&= \sqrt&lbrace;g(A_c/T_c)&rbrace; \\	
							\Rightarrow \left(\frac QA_c\right)^2	&= g(A_c/T_c) \\						 	
							\Rightarrow \frac &lbrace;Q^2&rbrace;g	&= \frac&lbrace;A_c^3&rbrace;&lbrace;T_c&rbrace;\\	
								&= \frac&lbrace;\left((\theta-\sin\theta)D^2/8\right)^3&rbrace;&lbrace;D\sin(\theta/2)&rbrace;	\\				
								&= \frac&lbrace;(\theta-\sin\theta)^3D^5&rbrace;&lbrace;512\cdot\sin(\theta/2)&rbrace;	\\
							\Rightarrow \frac &lbrace;{Q}^2&rbrace;&lbrace;{g}&rbrace;	&= \frac&lbrace;(\theta-\sin\theta)^3\cdot({sds(
							D
						)})^5&rbrace;&lbrace;512\cdot\sin(\theta/2)&rbrace;					
						\end&lbrace;aligned&rbrace; 
					$$
					This equation in !$\theta!$ cannot be solved analytically. You can find !$\theta!$ numerically using a solver on a calculator (in radian mode!) or using a spreadsheet:
					
					$$ \theta_c = {sdw(
							thetaCRad
						)}_&lbrace;\mathsf&lbrace;rad&rbrace;&rbrace; = {sdw(
							thetaC
						)}^\circ $$
					!$\theta_c>180^\circ!$ so the pipe is more than half full, and:	
					$$
						\begin&lbrace;aligned&rbrace;
							\alpha_c &= \left(360^\circ-\theta_c\right)\!/2 \\
							&= \left(360^\circ-{thetaC}^\circ\right)\!/2 \\
							&= {sdw((360 - thetaC) / 2)}^\circ \\
							\Rightarrow OA &= r\cos \alpha_c \\
							&= {sds(D / 2)}\cdot \cos {(360 - thetaC) / 2}^\circ \\
							&= {sdw(
							(D / 2) * Math.cos(toRadians((360 - thetaC) / 2))
						)}\,\mathsf&lbrace;m&rbrace; \\
						\Rightarrow y_c &= OA + r \\
						&= {sdw((D / 2) * Math.cos(toRadians((360 - thetaC) / 2)))} + {sds(D / 2)} \\
						&= {sdw(
							(D / 2) * Math.cos(toRadians((360 - thetaC) / 2)) + D / 2
						)}\,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace; 
					$$
					"
					/>
					<!-- {:else if sdw(thetaCRad) === sdw(Math.PI)} -->
				{:else if sds(thetaCRad) === sds(Math.PI)}
					<Card
						answer="For !$Q={sdw(
							Q
						)}\,\mathsf&lbrace;m^3\!/s&rbrace;!$, Critical Depth, !$y_c = {sds(
							yc,
							sdigs
						)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="Trial-and-error may be used, adjusting depth of flow !$y_c!$ until the depth yields an associated Froude number equal to !$1!$. This is somewhat lengthy and error-prone. Alternatively:
						
						$$
						\begin&lbrace;aligned&rbrace;
							N_F &= 1 \\
							\Rightarrow v_c	&= \sqrt&lbrace;g(A_c/T_c)&rbrace; \\	
							\Rightarrow \left(\frac QA_c\right)^2	&= g(A_c/T_c) \\						 	
							\Rightarrow \frac &lbrace;Q^2&rbrace;g	&= \frac&lbrace;A_c^3&rbrace;&lbrace;T_c&rbrace;\\	
								&= \frac&lbrace;\left((\theta-\sin\theta)D^2/8\right)^3&rbrace;&lbrace;D\sin(\theta/2)&rbrace;	\\				
								&= \frac&lbrace;(\theta-\sin\theta)^3D^5&rbrace;&lbrace;512\cdot\sin(\theta/2)&rbrace;	\\
							\Rightarrow \frac &lbrace;{Q}^2&rbrace;&lbrace;{g}&rbrace;	&= \frac&lbrace;(\theta-\sin\theta)^3\cdot({sds(
							D
						)})^5&rbrace;&lbrace;512\cdot\sin(\theta/2)&rbrace;					
						\end&lbrace;aligned&rbrace; 
					$$
					This equation in !$\theta!$ cannot be solved analytically. You can find !$\theta!$ numerically using a solver on a calculator (in radian mode!) or using a spreadsheet:
					
					$$ \theta_c = {sdw(
							thetaCRad
						)}_&lbrace;\mathsf&lbrace;rad&rbrace;&rbrace; = {sdw(
							thetaC
						)}^\circ \approx {sds(thetaC)}^\circ $$

					!$\theta_c={sds(thetaC)}^\circ!$ so the pipe is half full, and !$y_c = {sds(
							D / 2
						)}\,\mathsf&lbrace;m&rbrace;\\~!$ 
					
					"
					/>
				{:else}
					<Card
						answer="For !$Q={sdw(
							Q
						)}\,\mathsf&lbrace;m^3\!/s&rbrace;!$, Critical Depth: !$y_c = {sds(
							yc
						)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="Trial-and-error may be used, adjusting depth of flow !$y_c!$ until the depth yields an associated Froude number equal to !$1!$. This is somewhat lengthy and error-prone. Alternatively:
						
						$$
						\begin&lbrace;aligned&rbrace;
							N_F &= 1 \\
							\Rightarrow v_c	&= \sqrt&lbrace;g(A_c/T_c)&rbrace; \\	
							\Rightarrow \left(\frac QA_c\right)^2	&= g(A_c/T_c) \\						 	
							\Rightarrow \frac &lbrace;Q^2&rbrace;g	&= \frac&lbrace;A_c^3&rbrace;&lbrace;T_c&rbrace;\\	
								&= \frac&lbrace;\left((\theta-\sin\theta)D^2/8\right)^3&rbrace;&lbrace;D\sin(\theta/2)&rbrace;	\\				
								&= \frac&lbrace;(\theta-\sin\theta)^3D^5&rbrace;&lbrace;512\cdot\sin(\theta/2)&rbrace;	\\
							\Rightarrow \frac &lbrace;{Q}^2&rbrace;&lbrace;{g}&rbrace;	&= \frac&lbrace;(\theta-\sin\theta)^3\cdot({sds(
							D
						)})^5&rbrace;&lbrace;512\cdot\sin(\theta/2)&rbrace;					
						\end&lbrace;aligned&rbrace; 
					$$
					This equation in !$\theta!$ cannot be solved analytically. You can find !$\theta!$ numerically using a solver on a calculator (in radian mode!) or using a spreadsheet:
					
					$$ \theta_c = {sdw(
							thetaCRad
						)}_&lbrace;\mathsf&lbrace;rad&rbrace;&rbrace; = {sdw(
							thetaC
						)}^\circ $$
					!$\theta_c<180^\circ!$ so the pipe is less than half full, and:	
					$$
						\begin&lbrace;aligned&rbrace;
							\alpha_c &= \theta_c\!/2 \\
							&= {thetaC}^\circ\!/2 \\
							&= {sdw(thetaC / 2)}^\circ \\
							\Rightarrow OA &= r\cos \alpha_c \\
							&= {sds(D / 2)}\cdot \cos {thetaC / 2}^\circ \\
							&= {sdw((D / 2) * Math.cos(toRadians(thetaC / 2)))}\,\mathsf&lbrace;m&rbrace; \\
						\Rightarrow y_c &= r-OA \\
						&= {sds(D / 2)}-{sdw((D / 2) * Math.cos(toRadians(thetaC / 2)))}  \\
						&= {sdw(
							-(D / 2) * Math.cos(toRadians(thetaC / 2)) + D / 2
						)}\,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace; 
					$$
					"
					/>
				{/if}
				<Card
					answer="Critical Area: !$A_c = {sds(
						Ac
					)}  \,\mathsf&lbrace;m^2&rbrace; !$"
					solution="For !$y_c={yc}\,\mathsf&lbrace;m&rbrace;!$, !$\theta_c={thetaCRad}_\mathsf&lbrace;rad&rbrace;={thetaC}^\circ!$.
					
					$$
					\begin&lbrace;aligned&rbrace;
						A_c &= \frac&lbrace;\left(\theta_c-\sin\theta_c\right) D^2&rbrace;&lbrace;8&rbrace; \\
						 &= \frac&lbrace;\left({thetaCRad}-\sin{thetaC}^\circ\right) ({sds(
						D
					)})^2&rbrace;&lbrace;8&rbrace; \\

						 &= {sdw(
						((thetaCRad - Math.sin(thetaCRad)) * D ** 2) / 8
					)}\,\mathsf&lbrace;m^2&rbrace; 						
						 
					\end&lbrace;aligned&rbrace; $$"
				/>
				<Card
					answer="Critical Velocity: !$v_c = {sds(
						vc
					)}  \,\mathsf&lbrace;m^2&rbrace; !$"
					solution="					
					$$
					\begin&lbrace;aligned&rbrace;
						v_c &= Q/A_c	\\
						&= {Q}/{Ac}	\\
						&= {sdw(Q / Ac)} \,\mathsf&lbrace;m/s&rbrace; 						
						 
					\end&lbrace;aligned&rbrace; $$"
				/>
				<Card
					answer="Minimum Specific Energy: !$E_\mathsf&lbrace;min&rbrace; = {sds(
						Emin
					)}  \,\mathsf&lbrace;m&rbrace; !$"
					solution="					
					$$
					\begin&lbrace;aligned&rbrace;
						E_\mathsf&lbrace;min&rbrace; &= y_c+\frac&lbrace;v_c^2&rbrace;&lbrace;2g&rbrace;	\\
						&= {yc}+\frac&lbrace;({sdw(vc)})^2&rbrace;&lbrace;2({g})&rbrace;	\\
						&= {sdw(yc + (vc * vc) / 2 / g)} \,\mathsf&lbrace;m&rbrace; 						
						 
					\end&lbrace;aligned&rbrace; $$"
				/>
				<Card
					answer="Slope for critical flow: !$S_c = {sds(
						Sc
					)}  \,\mathsf&lbrace;\%&rbrace; !$"
					solution="					
					$$
					\begin&lbrace;aligned&rbrace;
						R_c &= \left[\frac&lbrace;\theta_c-\sin\theta_c&rbrace;&lbrace;\theta_c&rbrace;\right]\cdot\frac D4 \\
						&= \left[\frac&lbrace;{thetaCRad}-\sin {thetaC}^\circ &rbrace;&lbrace;{thetaCRad}&rbrace;\right]\cdot\frac&lbrace; {sds(
						D
					)} &rbrace;&lbrace;4&rbrace; \\
						&= {Rc}\,\mathsf&lbrace;m&rbrace; \\\\
						v_c &= \frac 1n R_c^&lbrace 2/3 &rbrace;S_c^&lbrace 1/2 &rbrace; \\
						\Rightarrow S_c &= \left(\frac &lbrace; nv_c &rbrace;&lbrace; R_c^&lbrace;2/3&rbrace; &rbrace;\right)^2 \\
						&= \left(\frac &lbrace; {n}\times{sdw(vc)} &rbrace;&lbrace; {sdw(
						Rc
					)}^&lbrace;2/3&rbrace; &rbrace;\right)^2\\
						&= {sdw(Sc / 100)} \\
						&= {sdw(Sc)}\% \\
					\end&lbrace;aligned&rbrace; $$"
				/>
			</section>
		{/if}
	</section>
</main>

