<script>
	import Card from "./Card.svelte";
	import Intro from "./Intro.svelte";
	import { fade } from "svelte/transition";

	let sdigs = 4,
		wdigs = 6,
		bs = 3,
		b,
		zls = 1.5,
		zl,
		zrs = 1.0,
		zr,
		ys = 1.2,
		ss = 0.1,
		s,
		n = 0.013,
		g = 9.81,
		y,
		yQ,
		A,
		AQ,
		T,
		TQ,
		WP,
		WPQ,
		R,
		RQ,
		Q,
		QQs = 3.25,
		QQ,
		v,
		vQ,
		E,
		EQ,
		NF,
		NFQ,
		yc,
		ycQ,
		Ac,
		AcQ,
		vc,
		vcQ,
		Emin,
		EminQ,
		WPc,
		WPcQ,
		Rc,
		RcQ,
		Sc,
		ScQ,
		// for interim algebra calculations for calculating y from Q
		num1,
		num1half,
		onePlusZLsquared,
		onePlusZRsquared,
		sumOfRoots,
		SrootOverN,
		coeff;

	let specifyY = true;
	let validS = true;
	let typeOfChannel = "trapezoidal";

	calculate();

	document.addEventListener("DOMContentLoaded", katexify);
	document.addEventListener("input", katexify);
	document.addEventListener("click", katexify);
	document.addEventListener("change", katexify);

	function calculate() {
		s = Number(ss);
		s = s < 0 ? -s : s;
		ss = sds(s); // for binding to the input field
		validS = s === 0 ? false : true;

		n = n <= 0 ? 0.01 : n;
		g = g < 9.8 ? 9.8 : g;

		b = Number(bs);
		b = b < 0 ? -b : b;
		bs = sds(b); // for binding to the input field

		zl = Number(zls);
		zl = zl < 0 ? -zl : zl;
		zls = sds(zl); // for binding to the input field

		zr = Number(zrs);
		zr = zr < 0 ? -zr : zr;
		zrs = sds(zr); // for binding to the input field

		b = b == 0 && zl == 0 && zr == 0 ? 0.1 : b;

		y = Number(ys);
		y = y < 0 ? -y : y;
		ys = sds(y); // for binding to the input field

		QQ = Number(QQs);
		QQ = QQ < 0 ? -QQ : QQ;
		QQs = sds(QQ); // for binding to the input field

		if (validS) {
			calculateFromY();
			calculateFromQ();
		}
	}

	function calculateFromY() {
		A = getArea(y);
		WP = getWP(y);
		R = getR(y);
		v = getV(y);
		Q = getQ(y);
		E = getE(y, v);
		T = getT(y);
		NF = getNF(v, A, T);
		yc = getYc(Q);
		Ac = getArea(yc);
		vc = getVc(Q, Ac);
		Emin = getE(yc, vc);
		WPc = getWP(yc);
		Rc = getR(yc);
		Sc = getCriticalSlope(n, vc, Rc);
	}

	function calculateFromQ() {
		yQ = getYfromQ();
		//simplification for algebra printout
		num1 = sdw(zl + zr);
		num1half = sdw(num1 / 2);
		onePlusZLsquared = sdw(1 + zl ** 2);
		onePlusZRsquared = sdw(1 + zr ** 2);
		sumOfRoots = sdw(onePlusZLsquared ** 0.5 + onePlusZRsquared ** 0.5);
		SrootOverN = sdw((s / 100) ** 0.5 / n);
		coeff = sdw((SrootOverN * num1half ** (5 / 3)) / sumOfRoots ** (2 / 3));
		//done printout
		AQ = getArea(yQ);
		vQ = getV2(QQ, AQ);
		EQ = getE(yQ, vQ);
		TQ = getT(yQ);
		NFQ = getNF(vQ, AQ, TQ);
		ycQ = getYc(QQ);
		AcQ = getArea(ycQ);
		vcQ = getVc(QQ, AcQ);
		EminQ = getE(ycQ, vcQ);
		WPcQ = getWP(ycQ);
		RcQ = getR(ycQ);
		ScQ = getCriticalSlope(n, vcQ, RcQ);
	}

	// https://www.freecodecamp.org/news/javascript-debounce-example/
	function debounce(func, timeout = 1500) {
		let timer;
		return (...args) => {
			clearTimeout(timer);
			timer = setTimeout(() => {
				func.apply(this, args);
			}, timeout);
		};
	}
	// stop katexify() running before calculate() has finished
	const delayK = debounce(() => {
		katexify();
	}, 10);

	const processChange = debounce(() => {
		calculate();
		delayK();
	});

	function getArea(y) {
		return Number(sdw(b + ((zl + zr) / 2) * y) * y);
	}

	function getWP(y) {
		return Number(sdw(b + y * ((1 + zl ** 2) ** 0.5 + (1 + zr ** 2) ** 0.5)));
	}

	function getR(y) {
		return Number(sdw(getArea(y) / getWP(y)));
	}

	function getV(y) {
		return Number(sdw((1 / n) * getR(y) ** (2 / 3) * (s / 100) ** 0.5));
	}

	function getV2(Q, A) {
		return Number(sdw(Q / A));
	}

	function getQ(y) {
		return Number(sdw(getArea(y) * getV(y)));
	}

	function getE(y, v) {
		return Number(sdw(y + v ** 2 / g / 2));
	}

	function getT(y) {
		return Number(sdw(b + (zl + zr) * y));
	}

	function getNF(v, A, T) {
		return Number(sdw(v / ((g * A) / T) ** 0.5));
	}
	function getNFfromY(y, Q) {
		return (Q * (getT(y) / g) ** 0.5) / getArea(y) ** 1.5;
	}

	function getYc(Q, shallow = 0, deep = 100) {
		const delta = 1 / 10 ** (wdigs + 1),
			mid = (deep + shallow) / 2;

		if (Math.abs(deep - shallow) < delta) {
			return Number(sdw((deep + shallow) / 2));
		}

		// do the search
		if (getNFfromY(mid, Q) < 1) {
			return getYc(Q, shallow, mid);
		} else {
			return getYc(Q, mid, deep);
		}
	}

	function getVc(Q, Ac) {
		return Number(sdw(Q / Ac));
	}

	function getCriticalSlope(n, vc, Rc) {
		return Number(sdw(((n * vc) / Rc ** (2 / 3)) ** 2 * 100));
	}

	function getYfromQ(underY = 0, overY = 100) {
		let delta = 1 / 10 ** (wdigs + 1),
			mid = (underY + overY) / 2;

		if (Math.abs(underY - overY) < delta) {
			return Number(sdw((underY + overY) * 0.5));
		}

		// search;
		if (getQ(mid) < QQ) {
			return getYfromQ(mid, overY);
		} else {
			return getYfromQ(underY, mid);
		}
	}

	function sd(num, sigDigs = sdigs) {
		return parseFloat(num).toPrecision(sigDigs);
	}

	function sdw(num) {
		return sd(num, wdigs);
	}

	function sds(num) {
		return sd(num, sdigs);
	}
</script>

<main>
	<section class="intro">
		<Intro {typeOfChannel} />

		<label class="yQlayout">
			<input type="radio" bind:group={specifyY} value={true} />
			<span class="radio__label">Specify !$y!$</span>
		</label>

		<label class="yQlayout">
			<input type="radio" bind:group={specifyY} value={false} />
			<span>Specify !$Q!$</span>
		</label>
	</section>

	<section class="fig">
		<div>
			<img
				src="./images/trapezoidalChannelSectionQ.png"
				alt="trapezoidal channel section"
			/>
			{#if specifyY}
				<img
					class="super"
					in:fade={{ duration: 750 }}
					out:fade={{ duration: 500 }}
					src="./images/trapezoidalChannelSectionY.png"
					alt="trapezoidal channel section by depth"
				/>
			{/if}

			<form on:submit|preventDefault={calculate}>
				<label class="b">
					!$b=!$
					<input
						type="number"
						step="any"
						required
						bind:value={bs}
						on:input={processChange}
					/>
					!$\mathsf&lbrace;m&rbrace;!$
				</label>
				<label class="zl">
					!$z_L=!$
					<input
						type="number"
						step="any"
						required
						bind:value={zls}
						on:input={processChange}
					/>
				</label>

				<label class="zr">
					!$z_R=!$
					<input
						type="number"
						step="any"
						required
						bind:value={zrs}
						on:input={processChange}
					/>
				</label>
				{#if specifyY}
					<label class="y">
						!$y=!$
						<input
							type="number"
							step="any"
							required
							bind:value={ys}
							on:input={processChange}
						/>
						!$\mathsf&lbrace;m&rbrace;!$
					</label>
				{:else}
					<label class="Q">
						!$Q=!$
						<input
							type="number"
							step="any"
							required
							bind:value={QQs}
							on:input={processChange}
						/>
						!$\mathsf&lbrace;m^3\!/s&rbrace;!$
					</label>
				{/if}
			</form>
		</div>
		<form on:submit|preventDefault={calculate}>
			<label class="s">
				Channel slope, !$S=!$
				<input
					type="number"
					step="any"
					required
					bind:value={ss}
					on:input={processChange}
				/>
				!$\small \% !$
			</label>
			<label class="n">
				Manning's !$n=!$
				<input
					type="number"
					step="any"
					required
					bind:value={n}
					on:input={calculate}
				/>
				!$\small \mathsf &lbrace; s/m^&lbrace;1/3&rbrace; &rbrace; !$
			</label>
			<label class="g">
				!$g=!$
				<input
					type="number"
					step="any"
					required
					bind:value={g}
					on:input={calculate}
				/>
				!$\small \mathsf &lbrace; m/s^2 &rbrace; !$
			</label>
		</form>
	</section>

	<section class="results">
		<!-- //////////////////////////////////////////////   FROM y   ///////////////////////// -->
		{#if specifyY}
			{#if !validS}
				<section class="message">
					For a slope of 0, there is no flow. And, without a flow, critical
					conditions are not defined.
				</section>
			{:else}
				<section class="normal">
					<h1>Normal (Uniform) Flow</h1>

					<Card
						answer="Flow Area: !$ A = {sds(A)} \mathsf &lcub; \,m^2 &rcub; !$"
						solution="$$
						\begin&lcub;aligned&rcub;
							A &= by+z_Ly\cdot\frac&lbrace;y&rbrace;&lbrace;2&rbrace;+z_Ry\cdot\frac&lbrace;y&rbrace;&lbrace;2&rbrace; \\
							 &= \left(b+&lcub;\left(\frac &lcub; z_L+z_R &rcub; &lcub; 2 &rcub; \right)&rcub;\cdot y \right) 		\cdot y \\
							&= \left({sds(b)}+&lcub;\left(\frac &lcub; {sds(zl)}+{sds(
							zr
						)} &rcub; &lcub; 2 &rcub; \right)&rcub;\cdot {y} 			\right) \cdot {y} \\
							&= {sdw(A)} \,\mathsf&lbrace;m^2&rbrace;
						\end&lbrace;aligned&rbrace; 
					$$"
					/>

					<Card
						answer="Wetted Perimeter: !$ P = {sds(WP)}\, \mathsf m !$"
						solution="$$
					\begin&lbrace;aligned&rbrace;
						WP &= b+\sqrt &lbrace; y^2+\left(z_L\cdot y\right)^2 &rbrace; + \sqrt &lbrace; y^2+\left(z_R\cdot y\right)^2 &rbrace; \\
						 &= b+\left(\sqrt &lbrace; 1+z_L^2 &rbrace; + \sqrt &lbrace; 1+z_R^2 &rbrace;\right)\cdot y \\
						&= {sds(b)}+\left(\sqrt &lbrace; 1+{sds(zl)}^2 &rbrace; + \sqrt &lbrace; 1+{sds(
							zr
						)}^2 &rbrace;\right)\cdot {sds(y)} \\
						&= {sdw(WP)} \,\mathsf&lbrace;m&rbrace;
					\end&lbrace;aligned&rbrace;
				$$"
					/>

					<Card
						answer="Hydraulic Radius: !$ R = {sds(R)}\, \mathsf m !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							R &= A/P \\
							&= {sdw(A)}/{sdw(WP)} \\
							&= {sdw(R)} \,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
					/>

					<Card
						answer="Average Flow Velocity: !$ v = {sds(
							v
						)}\, \mathsf &lbrace; m/s &rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							v &= \frac 1n R^&lbrace 2/3 &rbrace;S^&lbrace 1/2 &rbrace; \\
							&= \frac 1&lbrace; {n}&rbrace; ({sdw(R)})^&lbrace 2/3 &rbrace;({sds(
							s / 100
						)})^&lbrace; 1/2 &rbrace; \\
							&= {sdw(v)} \,\mathsf&lbrace;m/s&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
					/>

					<Card
						answer="Flow Rate: !$ Q = {sds(
							Q
						)}\,\mathsf &lbrace; m^3\!/s &rbrace !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							Q &= Av \\
							&= ({sdw(A)})\cdot({sdw(v)}) \\
							&= {sdw(Q)} \,\mathsf&lbrace;m^3\!/s&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
					/>

					<Card
						answer="Specific Energy: !$ E = {sds(
							E
						)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							E &= y+\frac &lbrace; v^2 &rbrace;&lbrace; 2g &rbrace; \\
							&= {sds(y)}+\frac &lbrace; {sdw(v)}^2 &rbrace;&lbrace; 2({g}) &rbrace; \\
							&= {sdw(E)}\,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
					/>

					<Card
						answer="Free Surface: !$ T = {sds(
							T
						)}\, \mathsf &lbrace; m &rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							T &= b+(z_L+z_R)\cdot y \\
							&= {sds(b)}+({sds(zl)}+{sds(zr)})\cdot {sds(y)} \\
							&= {sdw(T)}\,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace; 
					$$"
					/>

					<Card
						answer="Froude Number: !$N_F = {sds(NF)} !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							N_F &=  \frac&lbrace;v&rbrace;&lbrace;\sqrt&lbrace;g(A/T)&rbrace;&rbrace; \\
								&=  \frac&lbrace;{v}&rbrace;&lbrace;\sqrt&lbrace;{g}({sdw(A)}/{sdw(
							T
						)})&rbrace;&rbrace; \\
								&= {sdw(NF)}
						\end&lbrace;aligned&rbrace;
					$$"
					/>
				</section>
				<section class="critical">
					<h1>Critical Flow</h1>

					<Card
						answer="For !$Q={sdw(
							Q
						)}\,\mathsf&lbrace;m^3\!/s&rbrace;!$, Critical Depth: !$y_c = {sds(
							yc
						)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="$$
							\begin&lbrace;aligned&rbrace;
								N_F &= 1 \\
								\Rightarrow v_c &= \sqrt&lbrace; g(A_c/T_c)&rbrace; \\
								\Rightarrow \left(\frac&lbrace;Q&rbrace;&lbrace;A_c&rbrace;\right)^2 &= g(A_c/T_c) \\
								\Rightarrow \frac&lbrace;Q^2&rbrace;&lbrace;g&rbrace; &= \frac&lbrace;A_c^3&rbrace;&lbrace;T_c&rbrace; \\
								&= \frac&lbrace;\left(\left(b+&lcub;\left( z_L+z_R \right)&rcub;\cdot y_c/2 \right) \cdot y_c\right)^3&rbrace;&lbrace;b+(z_L+z_R)\cdot y_c&rbrace; \\
								\Rightarrow \frac&lbrace;{sdw(
							Q
						)}^2&rbrace;&lbrace;{g}&rbrace; &= \frac&lbrace;\left(\left({sds(
							b
						)}+&lcub;\left( {sds(zl)}+{sds(
							zr
						)} \right)&rcub;\cdot y_c/2 \right) \cdot y_c\right)^3&rbrace;&lbrace;{sds(
							b
						)}+({sds(zl)}+{sds(zr)})\cdot y_c&rbrace; \\
							\end&lbrace;aligned&rbrace;$$
					
						<div class='width85'>
						The expression above cannot be solved directly for !$y_c!$. It may be solved using trial and error methods but it is generally more convenient, and adequate, to solve it without further simplification using a numerical solver on a calculator or in a spreadsheet program(me). 
						</div>
						
						$$ y_c = {sdw(yc)} \,\mathsf&lbrace;m&rbrace; $$"
					/>

					<Card
						answer="Critical Velocity: !$v_c = {sds(
							vc
						)}  \,\mathsf&lbrace;m/s&rbrace; !$"
						solution="$$
					\begin&lbrace;aligned&rbrace;
						A_c &= \left(b+(z_L+z_R)\cdot y_c/2\right)\cdot y_c \\
						&= \left({sds(b)}+({sds(zl)}+{sds(zr)})\cdot {sds(yc)}/2\right)\cdot {sds(
							yc
						)} \\
						&= {sdw(Ac)} \,\mathsf&lbrace;m^2&rbrace;\\\\
						v_c &= Q/A_c \\
						&= {sdw(Q)}/{sdw(Ac)} \\
						&= {sdw(vc)} \,\mathsf&lbrace;m/s&rbrace;
					\end&lbrace;aligned&rbrace; $$"
					/>

					<Card
						answer="Minimum Specific Energy: !$ E_&lbrace;min&rbrace; = {sds(
							Emin
						)}\,\mathsf&lbrace;m&rbrace;!$"
						solution="$$
					\begin&lbrace;aligned&rbrace;
						E_&lbrace;min&rbrace; &= y_c+\frac &lbrace; v_c^2 &rbrace;&lbrace; 2g &rbrace; \\
						&= {sdw(yc)}+\frac &lbrace; {sdw(vc)}^2 &rbrace;&lbrace; 2({g}) &rbrace; \\
						&= {sdw(Emin)} \,\mathsf&lbrace;m&rbrace;
					\end&lbrace;aligned&rbrace;
				$$"
					/>

					<Card
						answer="Slope for Critical Flow: !$ S_c = {sds(
							Sc
						)}\,\mathsf&lbrace;\%&rbrace; !$"
						solution="$$
					\begin&lbrace;aligned&rbrace;
						A_c &= \left(b+&lcub;\left( z_L+z_R \right)&rcub;\cdot y_c/2 \right) \cdot y_c \\
						&= \left({sds(b)}+&lcub;\left( {sds(zl)}+{sds(zr)} \right)&rcub;\cdot {sdw(
							yc
						)}/2 \right) \cdot {sdw(yc)} \\
						&= {sdw(Ac)} \,\mathsf&lbrace;m^2&rbrace;\\\\

						P_c &= b+\left(\sqrt &lbrace; 1+z_L^2 &rbrace; + \sqrt &lbrace; 1+z_R^2 &rbrace;\right)\cdot y_c \\
						&= {sds(b)}+\left(\sqrt &lbrace; 1+{sds(zl)}^2 &rbrace; + \sqrt &lbrace; 1+{sds(
							zr
						)}^2 &rbrace;\right)\cdot {sds(yc)} \\
						&= {sdw(WPc)} \,\mathsf&lbrace;m&rbrace;\\\\						

						R_c &= A_c/P_c \\
						&= {sdw(Ac)}/{sdw(WPc)} \\
						&= {sdw(Rc)} \,\mathsf&lbrace;m&rbrace;\\\\
						v_c &= \frac 1n R_c^&lbrace 2/3 &rbrace;S_c^&lbrace 1/2 &rbrace; \\
						\Rightarrow S_c &= \left(\frac &lbrace; nv_c &rbrace;&lbrace; R_c^&lbrace;2/3&rbrace; &rbrace;\right)^2 \\
						&= \left(\frac &lbrace; {n}\times{vc} &rbrace;&lbrace; {sdw(
							Rc
						)}^&lbrace;2/3&rbrace; &rbrace;\right)^2\\
						&= {sdw(Sc / 100)} \\
						&= {sdw(Sc)}\% \\
					\end&lbrace;aligned&rbrace;
				$$"
					/>
				</section>
			{/if}
		{/if}

		<!-- //////////////////////////////////////////   FROM Q   ///////////////////////// -->
		{#if !specifyY}
			{#if !validS}
				<section class="message">
					For a slope of 0, there is no normal flow and depth can not be
					calculated. But slope is not required for critical flow conditions to
					be determined.
				</section>
			{:else}
				<section class="normal">
					<h1>Normal (Uniform) Flow</h1>

					<Card
						answer="Depth of flow: !$ y = {sds(
							yQ
						)} \mathsf &lcub; \,m &rcub; !$"
						solution="$$
					\begin&lcub;aligned&rcub;
						Q &= \frac 1n AR^&lbrace; 2/3 &rbrace;S^&lbrace; 1/2 &rbrace; \\
							&= \frac 1n A(A/P)^&lbrace; 2/3 &rbrace;S^&lbrace; 1/2 &rbrace; \\
							&= \frac 1n \cdot \frac&lbrace;A^&lbrace;5/3&rbrace;&rbrace;&lbrace;P^&lbrace;2/3&rbrace;&rbrace;\cdot S^&lbrace; 1/2 &rbrace; \\
							&= \frac 1n \cdot \frac&lbrace;\left(\left(b+\left( z_L+z_R \right)\!/2\cdot y \right) \cdot y\right)^&lbrace;5/3&rbrace;&rbrace;&lbrace;\left(b+\left(\sqrt &lbrace; 1+z_L^2 &rbrace; + \sqrt &lbrace; 1+z_R^2 &rbrace;\right)\cdot y\right)^&lbrace;2/3&rbrace;&rbrace;\cdot S^&lbrace; 1/2 &rbrace; \\
						\Rightarrow {sds(
							QQ
						)} &= \frac 1&lcub;{n}&rcub; \cdot \frac&lbrace;\left(\left({sds(
							b
						)}+\left( {sds(zl)}+{sds(
							zr
						)} \right)\!/2\cdot y \right) \cdot y\right)^&lbrace;5/3&rbrace;&rbrace;&lbrace;\left({sds(
							b
						)}+\left(\sqrt &lbrace; 1+{sds(
							zl
						)}^2 &rbrace; + \sqrt &lbrace; 1+{sds(
							zr
						)}^2 &rbrace;\right)\cdot y\right)^&lbrace;2/3&rbrace;&rbrace;\cdot\left( {sds(
							s / 100
						)}\right)^&lbrace; 1/2 &rbrace; 		
					\end&lbrace;aligned&rbrace; 
					$$
					<div class='width85'>
					The expression above cannot be solved directly for !$y!$. It may be solved using trial and error methods but it is generally more convenient, and adequate, to solve it without further simplification using a numerical solver on a calculator or in a spreadsheet program(me). </div>
					$$ y = {sdw(yQ)} \,\mathsf&lbrace;m&rbrace; $$
					"
					/>

					<Card
						answer="Flow Area: !$ A = {sds(AQ)} \mathsf &lcub; \,m^2 &rcub; !$"
						solution="$$
						\begin&lcub;aligned&rcub;
							A &= \left(b+&lcub;\left(\frac &lcub; z_L+z_R &rcub; &lcub; 2 &rcub; \right)&rcub;\cdot y \right) 		\cdot y \\
							&= \left({sds(b)}+&lcub;\left(\frac &lcub; {sds(zl)}+{sds(
							zr
						)} &rcub; &lcub; 2 &rcub; \right)&rcub;\cdot {sdw(
							yQ
						)} 			\right) \cdot {sdw(yQ)} \\
							&= {sdw(AQ)} \,\mathsf&lbrace;m^2&rbrace;
						\end&lbrace;aligned&rbrace; 
					$$"
					/>

					<Card
						answer="Average Flow Velocity: !$ v = {sds(
							vQ
						)}\, \mathsf &lbrace; m/s &rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							v &= Q/A \\
							v &= {sds(QQ)}/{sdw(AQ)} \\					
							&= {sdw(vQ)} \,\mathsf&lbrace;m/s&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
					/>

					<Card
						answer="Specific Energy: !$ E = {sds(
							EQ
						)} \,\mathsf&lbrace;m&rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							E &= y+\frac &lbrace; v^2 &rbrace;&lbrace; 2g &rbrace; \\
							&= {sdw(yQ)}+\frac &lbrace; {sdw(vQ)}^2 &rbrace;&lbrace; 2({g}) &rbrace; \\
							&= {sdw(EQ)}\,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
					/>

					<Card
						answer="Free Surface: !$ T = {sds(
							TQ
						)}\, \mathsf &lbrace; m &rbrace; !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							T &= b+(z_L+z_R)\cdot y \\
							&= {sds(b)}+({sds(zl)}+{sds(zr)})\cdot {sdw(yQ)} \\
							&= {sdw(TQ)}\,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace; 
				$$"
					/>

					<Card
						answer="Froude Number: !$N_F = {sds(NFQ)} !$"
						solution="$$
						\begin&lbrace;aligned&rbrace;
							N_F &=  \frac&lbrace;v&rbrace;&lbrace;\sqrt&lbrace;g(A/T)&rbrace;&rbrace; \\
								&=  \frac&lbrace;{sdw(vQ)}&rbrace;&lbrace;\sqrt&lbrace;{g}({sdw(AQ)}/{sdw(
							TQ
						)})&rbrace;&rbrace; \\
								&= {sdw(NFQ)}
						\end&lbrace;aligned&rbrace;
					$$"
					/>
				</section>
			{/if}
			<section class="critical">
				<h1>Critical Flow</h1>

				<Card
					answer="For !$Q={sds(
						QQ
					)}\,\mathsf&lbrace;m^3\!/s&rbrace;!$, Critical Depth: !$y_c = {sds(
						ycQ
					)} \,\mathsf&lbrace;m&rbrace; !$"
					solution="$$
						\begin&lbrace;aligned&rbrace;
							N_F &= 1 \\
							\Rightarrow v_c &= \sqrt&lbrace; g(A_c/T_c)&rbrace; \\
							\Rightarrow \left(\frac&lbrace;Q&rbrace;&lbrace;A_c&rbrace;\right)^2 &= g(A_c/T_c) \\
							\Rightarrow \frac&lbrace;Q^2&rbrace;&lbrace;g&rbrace; &= \frac&lbrace;A_c^3&rbrace;&lbrace;T_c&rbrace; \\
							&= \frac&lbrace;\left(\left(b+&lcub;\left( z_L+z_R \right)&rcub;\cdot y_c/2 \right) \cdot y_c\right)^3&rbrace;&lbrace;b+(z_L+z_R)\cdot y_c&rbrace; \\
							\Rightarrow \frac&lbrace;{sds(
						QQ
					)}^2&rbrace;&lbrace;{g}&rbrace; &= \frac&lbrace;\left(\left({sds(
						b
					)}+&lcub;\left( {sds(zl)}+{sds(
						zr
					)} \right)&rcub;\cdot y_c/2 \right) \cdot y_c\right)^3&rbrace;&lbrace;{sds(
						b
					)}+({sds(zl)}+{sds(zr)})\cdot y_c&rbrace; \\
						\end&lbrace;aligned&rbrace;$$
						<div class='width85'>
						The expression above cannot be solved directly for !$y_c!$. It may be solved using trial and error methods but it is generally more convenient, and adequate, to solve it without further simplification using a numerical solver on a calculator or in a spreadsheet program(me).
						</div>
						
						$$ y_c = {sdw(ycQ)} \,\mathsf&lbrace;m&rbrace; $$ 

						"
				/>

				<Card
					answer="Critical Velocity, !$v_c = {sds(
						vcQ
					)}  \,\mathsf&lbrace;m/s&rbrace; !$"
					solution="$$
						\begin&lbrace;aligned&rbrace;
							A_c &= \left(b+(z_L+z_R)\cdot y_c/2\right)\cdot y_c \\
							&= \left({sds(b)}+({sds(zl)}+{sds(zr)})\cdot {sdw(ycQ)}/2\right)\cdot {sdw(
						ycQ
					)} \\
							&= {sdw(AcQ)} \,\mathsf&lbrace;m^2&rbrace;\\\\
							v_c &= Q/A_c \\
							&= {sds(QQ)}/{sdw(AcQ)} \\
							&= {sdw(vcQ)} \,\mathsf&lbrace;m/s&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
				/>

				<Card
					answer="Minimum Specific Energy, !$ E_&lbrace;min&rbrace; = {sds(
						EminQ
					)}\,\mathsf&lbrace;m&rbrace;!$"
					solution="$$
						\begin&lbrace;aligned&rbrace;
							E_&lbrace;min&rbrace; &= y_c+\frac &lbrace; v_c^2 &rbrace;&lbrace; 2g &rbrace; \\
							&= {sdw(ycQ)}+\frac &lbrace; {sdw(vcQ)}^2 &rbrace;&lbrace; 2({g}) &rbrace; \\
							&= {sdw(EminQ)} \,\mathsf&lbrace;m&rbrace;
						\end&lbrace;aligned&rbrace;
					$$"
				/>

				<Card
					answer="Slope for Critical Flow, !$ S_c = {sds(
						ScQ
					)}\,\mathsf&lbrace;\%&rbrace; !$"
					solution="$$
					\begin&lbrace;aligned&rbrace;
						A_c &= \left(b+&lcub;\left( z_L+z_R \right)&rcub;\cdot y_c/2 \right) \cdot y_c \\
						&= \left({sds(b)}+&lcub;\left( {sds(zl)}+{sds(zr)} \right)&rcub;\cdot {sdw(
						ycQ
					)}/2 \right) \cdot {ycQ} \\
						&= {sdw(AcQ)} \,\mathsf&lbrace;m^2&rbrace;\\\\

						P_c &= b+\left(\sqrt &lbrace; 1+z_L^2 &rbrace; + \sqrt &lbrace; 1+z_R^2 &rbrace;\right)\cdot y_c \\
						&= {sds(b)}+\left(\sqrt &lbrace; 1+{sds(zl)}^2 &rbrace; + \sqrt &lbrace; 1+{sds(
						zr
					)}^2 &rbrace;\right)\cdot {sdw(ycQ)} \\
						&= {sdw(WPcQ)} \,\mathsf&lbrace;m&rbrace;\\\\					

						R_c &= A_c/P_c \\
						&= {sdw(AcQ)}/{sdw(WPcQ)} \\
						&= {sdw(RcQ)} \,\mathsf&lbrace;m&rbrace;\\\\
						v_c &= \frac 1n R_c^&lbrace 2/3 &rbrace;S_c^&lbrace 1/2 &rbrace; \\
						\Rightarrow S_c &= \left(\frac &lbrace; nv_c &rbrace;&lbrace; R_c^&lbrace;2/3&rbrace; &rbrace;\right)^2 \\
						&= \left(\frac &lbrace; {n}\times{sdw(vcQ)} &rbrace;&lbrace; {sdw(
						RcQ
					)}^&lbrace;2/3&rbrace; &rbrace;\right)^2\\
						&= {sdw(ScQ / 100)} \\
						&= {sdw(ScQ)}\% \\
					\end&lbrace;aligned&rbrace;
				$$"
				/>
			</section>
		{/if}
	</section>
</main>

